@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header{width:100vw;height:25rem;background:#abd0ce;display:flex}.Header .text{color:white;font-size:5rem;font-family:'Poppins', sans-serif;margin:3rem 0 0 5rem}

.Title{color:#7c7877;font-size:1.8rem;display:flex;margin-bottom:1rem}.Title .text{font-family:'Nunito', sans-serif}.Title svg{margin-top:0.3rem;margin-left:0.3rem}

.Education{margin:5rem 3rem 5rem 3rem;display:flex;flex-direction:column;align-items:center}.Education .Education-content{display:flex}.Education .Education-content ul{margin-left:-1rem;margin-top:0;color:#63605f;font-family:'Noto Sans KR', sans-serif;font-size:1rem}.Education .Education-content ul li{margin-bottom:0.5rem}

.Profile{margin:3rem 3rem 5rem 3rem;display:flex;flex-direction:column;align-items:center}.Profile .Profile-content{display:flex}.Profile .Profile-content img{width:13rem}.Profile .Profile-content ul{list-style:none;margin-left:-1rem;margin-top:0;color:#63605f;font-family:'Noto Sans KR', sans-serif;font-size:1.2rem}

.PrivateLesson{margin:5rem 3rem 5rem 3rem;display:flex;flex-direction:column;align-items:center}.PrivateLesson .PrivateLesson-content{display:flex}.PrivateLesson .PrivateLesson-content ul{margin-left:-1rem;margin-top:0;color:#63605f;font-family:'Noto Sans KR', sans-serif;font-size:1rem}.PrivateLesson .PrivateLesson-content ul li{margin-bottom:0.5rem}

.Body hr{width:30%;border-top:1px solid #e2dcda}

.Club{margin:5rem 3rem 5rem 3rem;display:flex;flex-direction:column;align-items:center}.Club .Club-content{display:flex}.Club .Club-content ul{margin-left:-1rem;margin-top:0;color:#63605f;font-family:'Noto Sans KR', sans-serif;font-size:1rem}.Club .Club-content ul li{margin-bottom:0.5rem}

.Contact{margin:5rem 3rem 5rem 3rem;display:flex;flex-direction:column;align-items:center}.Contact .Contact-content{display:flex}.Contact .Contact-content ul{text-align:center;list-style:none;margin-left:-3.4rem;margin-top:0;color:#63605f;font-family:'Noto Sans KR', sans-serif;font-size:1rem}.Contact .Contact-content ul li{display:flex;justify-content:center;margin-bottom:0.5rem}.Contact .Contact-content ul li a:link{color:#63605f}.Contact .Contact-content ul li a:visited{color:#63605f}.Contact .Contact-content ul li svg{font-size:1.3rem;margin-top:0.1rem;margin-right:0.3rem}.Contact .Contact-content ul li svg:hover{color:#807c7b}

